* {
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-weight: bold;
}

ol, ul, li {
  list-style: none;
}

a {
  text-decoration: none;
}

.wrap {
  display: none;
}

.fadein {
  display: block;
  animation: fadein_wrap 0.7s forwards;
  animation-delay: 0.1s !important;
  opacity: 0;
}

@keyframes fadein_wrap {
  0% {
    -webkit-transform: translate3d(0, 1000px, 0);
    transform: translate3d(0, 1000px, 0);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

.fadeout {
  display: block;
  animation: fadeout .7s forwards;
}

@keyframes fadeout {
  0% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 0;
  }
}

.intro {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 9999;
  display: none;
}

.intro .bgL {
  float: left;
  width: 50%;
  height: 100%;
  background-color: #3e3e3e;
}

.intro .bgR {
  float: right;
  width: 50%;
  height: 100%;
  background-color: #3e3e3e;
}

.intro .main_tit {
  position: absolute;
  top: 38%;
  left: 0;
  right: 0;
  margin: auto;
  text-align: center;
  color: #fff;
  font-size: 150px;
  animation: trackingIn 1s forwards;
  animation-delay: !important;
  opacity: 0;
}

@keyframes trackingIn {
  0% {
    letter-spacing: -0.5em;
    transform: translate(0, 0);
    opacity: 0;
  }
  100% {
    transform: translate(0, 0);
    opacity: 1;
  }
}

.intro .mouse {
  position: absolute;
  left: 0;
  right: 0;
  top: 80%;
  margin: auto;
  width: 30px;
  height: 45px;
  border-radius: 50px;
  border: 1px solid white;
  box-sizing: border-box;
  animation: mouse 1s forwards;
  animation-delay: !important;
  opacity: 0;
}

@keyframes mouse {
  0% {
    -webkit-transform: translate3d(0, 1000px, 0);
    transform: translate3d(0, 1000px, 0);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

.intro .mouse span {
  display: block;
  background-color: white;
  width: 14px;
  height: 16px;
  border-top-left-radius: 10px;
  border-bottom-right-radius: 3px;
  animation: flash 1.5s infinite;
  animation-delay: !important;
}

@keyframes flash {
  from,
  50%,
  to {
    opacity: 1;
  }
  25%,
  75% {
    opacity: 0;
  }
}

.bg_ani_open {
  animation: bg .8s forwards;
}

.bg_ani_close {
  animation: bg2 .8s forwards;
}

@keyframes bg {
  0% {
    width: 50%;
  }
  100% {
    width: 0%;
  }
}

@keyframes bg2 {
  0% {
    width: 0%;
  }
  100% {
    width: 50%;
  }
}

